import React, { Component } from 'react';
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import { PainelContainer } from "./styles";
import { Title } from "../../components/titles/styles";
import { Card, ClientCard } from "../../components/cards/styles";
import avatarIcon from "../../assets/avatar-admin.png";

import * as customersActions from '../../store/actions'; 

class Home extends Component {
  componentDidMount() {
    this.props.requestCustomersList();
    console.log(`customersList: ${this.props.customers}`);
  }

  render() {
    return (
      <PainelContainer>
        <Title color={"#2074f8"}>Visão geral</Title>
        <Card>
          <p>Total de Clientes</p>
          <strong>7</strong>
        </Card>
        <Title color={"#2074f8"}>Clientes cadastrados</Title>
        <ClientCard>
          <img src={avatarIcon} alt="Cliente Um" />
          <p>Nome do Cliente</p>
          <p>Email do Cliente</p>
          <p>Email do Telefone do Cliente</p>
          <p>Status do Cliente</p>
        </ClientCard>
      </PainelContainer>
    );
  }
}

const mapStateToProps = state => ({
  customers: state.data,
  loading: state.loading,
  error: state.error
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(customersActions, dispatch);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home);